

/*
 * Default variables
 */

$val-sm: 50px;
$val-md: 80px;
$val-lg: 100vw;




/**
 * Duration
 */

[data-aos] {
  @for $i from 1 through 60 {
    body[data-aos-duration='#{$i*50}'] &,
    &[data-aos][data-aos-duration='#{$i*50}'] {
      transition-duration: #{$i*50}ms;
    }
  }
}

