    transition: all 1500ms ease; }
  body.scrolled:before {
    border-left: 20px solid rgba(0, 0, 0, 0.8); }
  body:after {
    content: "";
    display: block;
    position: fixed;
    z-index: 2;
    left: 0;
    top: 0;
    width: 100%;
    height: 60px;
    border-top: 0 solid rgba(0, 0, 0, 0.4);
    transition: all 1500ms ease; }
  body.scrolled:after {
    border-top: 60px solid rgba(0, 0, 0, 0.8); }

